export const FILE_SELECT = Symbol('FILE_SELECT');

export const FILE_REMOVE = Symbol('FILE_REMOVE');

export const FILE_UPDATE = Symbol('FILE_UPDATE');

export const IMPORT_MEMO = Symbol('IMPORT_MEMO');

export function selectFile() {
  return {
    type: FILE_SELECT,
  };
}

export function removeFile(file) {
  return {
    type: FILE_REMOVE,
    payload: {
      node: file,
    },
  };
}

export function updateFile(file, data) {
  return {
    type: FILE_UPDATE,
    payload: {
      node: file,
      data,
    },
  };
}

export function importMemo() {
  return {
    type: IMPORT_MEMO,
  };
}
